<template>
  <div>
    <router-link class="header-abs" to="/" tag="div" v-show="showAbs">
      <span class="iconfont iconfont-left">&#xe624;</span>
    </router-link>
    <router-link class="header-fixed" tag="div" to="/" v-show="!showAbs" :style="opacityStyle">
      <span class="iconfont header-iconfont">&#xe624;</span>
      ÊôØÁÇπËØ¶ÊÉÖ
    </router-link>
  </div>
</template>

<script>
export default {
  name: "DetailHeader",
  data() {
    return {
      showAbs: true,
      // Áªôheader-fixedÁªëÂÆö‰∏Ä‰∏™ÂàùÂßãÊ†∑Âºè
      opacityStyle: {
        // opacity‰∏∫0Êó∂ÂÆåÂÖ®ÈöêËóèÔºå‰∏∫1Êó∂ÂÆåÂÖ®ÊòæÁé∞
        opacity: 0
      }
    };
  },
  methods: {
    handlScroll() {
      // Ëé∑ÂèñÈ°µÈù¢È°∂Á´ØÊªöÂä®Êó∂Ë∑ùÈ°∂Á´ØÁöÑË∑ùÁ¶ª
      var top = document.documentElement.scrollTop;
      if (top > 60) {
        this.showAbs = false;
        // Âú®Ë∑ùÁ¶ªÈ°∂ÈÉ®60Ëá≥140‰πãÈó¥ËææÂà∞Ê∏êÈöêÊ∏êÁé∞ÁöÑÊïàÊûú
        let opacity = top / 140;
        opacity = opacity > 1 ? 1 : opacity;
        this.opacityStyle = { opacity };
      } else {
        this.showAbs = true;
      }
      // console.log(top);
    }
  },
  // Âõ†‰∏∫È°µÈù¢Áî®‰æãkeep-aliveÔºåÊâÄÊúâÈ°µÈù¢‰∏ÄÂ±ïÁ§∫Â∞±‰ºöÊâßË°åÁöÑÁîüÂëΩÂë®ÊúüÈí©Â≠ê
  activated() {
    // ÁõëÂê¨È°µÈù¢ÊªöÂä®Êó∂ÁöÑÁä∂ÊÄÅ
    window.addEventListener("scroll", this.handlScroll);
  },
  // Âú®ÈÄÄÂá∫ÂΩìÂâçÈ°µÈù¢Êó∂ÊâßË°åÁöÑÁîüÂëΩÂë®ÊúüÈí©Â≠ê
  deactivated() {
    // ÈÄÄÂá∫ÂΩìÂâçÈ°µÈù¢Êó∂ÔºåÂØπÈ°µÈù¢ÊòæÁ§∫Êó∂ÁöÑ‰∫ã‰ª∂Ëß£Áªë
    window.removeEventListener("scroll", this.handlScroll);

  }
};
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.stylus';

.header-abs {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 10px;
  left: 10px;
  text-align: center;

  .iconfont-left {
    color: #fff;
    font-size: 24px;
  }
}

.header-fixed {
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  line-height: 0.86rem;
  background: $bgColor;
  text-align: center;
  color: #ffffff;
  font-size: 18px;

  .header-iconfont {
    position: absolute;
    top: 0;
    left: 0;
    width: 0.64rem;
    font-size: 0.4rem;
  }
}
</style>



