import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home/Home.vue'
import City from '../pages/city/City.vue'
import Detail from '../pages/detail/Detail.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/city',
      name: 'City',
      component: City
    },
    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detail
    },
  ],
  // ÂΩìÂàáÊç¢È°µÈù¢Êó∂ÔºåÊªöÂä®‰ºöÂØπ‰∏ã‰∏Ä‰∏™È°µÈù¢ÊúâÂΩ±ÂìçÔºåÊâÄ‰ª•ÈúÄË¶ÅÊ≠§È°πÊù•‰ΩøË∑≥ËΩ¨Âà∞‰∏ã‰∏Ä‰∏™È°µÈù¢Êó∂ÔºåÂõûÂà∞È°µÈù¢È°∂Á´Ø
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  }
})



